@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .card {
    @apply bg-white p-6 rounded-lg shadow;
  }
  .btn {
    @apply px-4 py-2 rounded font-medium;
  }
  .btn-primary {
    @apply bg-primary text-white hover:bg-blue-700;
  }
  .section-header {
    @apply text-xl font-semibold mb-4;
  }
  .progress-bar {
    @apply h-2 bg-primary rounded-full;
  }
}
